<template>
    <div class="home-index">
        <div class="left-container">
            <HomeIndexLeftNav
                :leftNavList="leftNavList"
                :handleItem="handleItem"
            />
        </div>
        <div class="middle-container">
            <transition name="slide">
                <keep-alive>
                    <component
                        :handleView="handleView"
                        :is="component"
                    ></component>
                </keep-alive>
            </transition>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            component: "MyPageBlog",

            leftNavList: [
                {
                    id: 1,
                    title: "个人中心",
                    component: "MyPageInfo",
                    isActive: false
                },
                {
                    id: 2,
                    title: "我的收藏",
                    component: "MyPageLiked",
                    isActive: false
                },
                {
                    id: 3,
                    title: "我的博客",
                    component: "MyPageBlog",
                    isActive: true
                },
                {
                    title: "我的关注",
                    id: 4,
                    component: "MyPageFocusPeople",
                    isActive: false
                }
            ]
        };
    },
    created() {},
    watch: {
        $route: {
            handler: function(to, from) {
                //根据key 获取类型
                if (!to) {
                    let key = from.query.key;
                } else {
                    let key = to.query.key;
                    if (!key) return;
                    this.leftNavList.forEach(v =>
                        v.title === key
                            ? ((v.isActive = true),
                              (this.component = v.component))
                            : (v.isActive = false)
                    );
                }
            },
            immediate: true
        }
    },
    methods: {
        handleItem(e) {
            let key = e.target.dataset.key;
            this.leftNavList.forEach(v => {
                if (v.id == key) {
                    if (v.isActive === true) return;
                    v.isActive = true;
                    this.$router.push("/my-page?key=" + v.title);
                } else {
                    v.isActive = false;
                }
            });
        },
        handleView(id) {
            this.$router.push("/show-blog/" + id);
        }
    }
};
</script>

<style lang="scss" scoped>
.home-index {
    display: flex;
    width: 1180px;
    margin: 0 auto;
    flex-direction: row;
    position: relative;
    padding-top: 56px;
    margin-bottom: 12px;
    .middle-container {
        width: 1072px;
        min-height: 450px;
        background-color: #fff;
        box-sizing: border-box;
        padding: 2rem;
    }
}
</style>